*,
*::before,
*::after {
    box-sizing: border-box;
}
* {
    margin: 0;
    padding: 0;
}

body {
    line-height: 1.5;
}

img,
picture,
video,
canvas,
svg {
    display: block;
    max-width: 100%;
}
input,
button,
textarea,
select {
    font: inherit;
    outline: none;
}
p,
h1,
h2,
h3,
h4,
h5,
h6 {
    overflow-wrap: break-word;
}
.App {
    position: relative;
    width: 100%;
    height: 100%;
    padding-top: 50px;
    min-height: 100vh;
    /* overflow: hidden; */
}

.App.theme-dark {
    transition: all 0.25s ease-in-out;
    background-color: rgb(3, 3, 3);
}
.App.theme-light {
    transition: all 0.25s ease-in-out;
    background-color: #fff;
}

.f-bold {
    font-weight: bold;
}

.btn {
    border: none;
    outline: none;
    padding: 8px 20px;
    cursor: pointer;
    font-weight: 500;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
    transition: all 0.25s linear;
    border-radius: 20px;
}
.btn:hover {
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
}

.input-todo {
    margin: 0px auto;
    background-color: #fff;
    padding: 10px 20px;
    border-radius: 30px;
    transition: all 0.25s ease-in-out;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    max-width: 500px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.input-todo.theme-dark {
    background-color: #333;
}

.input-todo.theme-dark input {
    color: rgb(237, 235, 235);
}

.input-todo input {
    font-size: 20px;
    font-weight: 500;
    width: 70%;
    border: none;
    outline: none;
    background-color: transparent;
    transition: all 0.25s ease-in-out;
}

.input-todo input::placeholder {
    color: #ccc;
}

.input-todo-content {
    border: 1px solid #eee;
    padding: 5px 10px;
    outline: none;
}

.btn-submit {
    background-color: #20e3b2;
    color: #fff;
}

.btn-submit.theme-dark {
    background-color: #1abe95;
    color: #fff;
}

.btn-toggle-theme {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    padding: 10px;
    border-radius: 1000px;
    bottom: 50px;
    right: 50px;
    transition: all 0.8s linear;
    background-color: rgb(235, 231, 231);
    color: #000;
}

/* .btn-toggle-theme ion-icon {
    animation: rotate 0.5s ease-in-out;
} */

.btn-toggle-theme.is-dark {
    background-color: rgb(73, 67, 67);
    color: #fff;
}

@keyframes rotate {
    100% {
        transform: rotate(360deg);
    }
}

@media (max-width: 768px) {
    .App {
        margin: 0;
        padding: 20px;
    }
    .input-todo input {
        font-size: 16px;
        margin-right: 10px;
    }
    .input-todo {
        width: 100%;
    }
    .input-todo-content {
        border-radius: 10px;
    }
    .btn-toggle-theme {
        right: 10px;
        bottom: 100px;
    }
}
